.profile__subtitle {
  color: white;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 300px;
}

@media screen and (max-width: 675px) {
  .profile__subtitle {
    font-size: 14px;
    line-height: 17px;
  }
}
