.popup__container {
  display: flex;
  justify-items: center;
  flex-direction: column;
  width: 430px;
  background-color: white;
  margin: 0 auto;
  border-radius: 5px;
  position: relative;
}

.popup__container:hover {
  cursor: default;
}

@media screen and (max-width: 630px) {
  .popup__container {
    width: 280px;
  }
}